<template>
    <div class="login-container">
        <p class="login-title">{{ agreeTitle }}</p>
        <div class="content" v-html="content"></div>
    </div>
</template>
<script>
    import apiService from '@/config/web/apiService'
    import {
        API_URL_GET_MEMBER_AGREE,
    } from '@/config/web/urls'
    import JQ from "jquery";
    import {mapActions, mapGetters} from "vuex";

    export default {
        name: 'memberAgree',
        computed: {
            ...mapGetters('config', ["getAgree", "getSettingActive"]),
        },
        watch: {
            '$route'(to, from) {
                this.code = this.$route.params.code;
                this.reload()
            },
            getSettingActive(val) {
                if (val) {
                    this.getData();
                }
            }

        },
        data() {
            return {
                agreeTitle: '이용약관',
                content: '',
                privacyContent: '',
                useContent: '',
                code: '',
                mobileClass: '',

            };
        },
        created() {


            this.code = this.$route.params.code;
            if (this.code == 'privacy') {
                this.agreeTitle = '개인정보처리방침'
            }
            if(this.getSettingActive) {
                this.getData();
            }

        },
        methods: {
            getData() {
                this.privacyContent = this.getAgree.privacy.content;
                this.useContent = this.getAgree.use.content;
                this.reload();
            },
            reload() {

                if (this.code == 'privacy') {
                    this.agreeTitle = '개인정보처리방침'
                    this.content = this.privacyContent;
                } else {
                    this.content = this.useContent;
                    this.agreeTitle = '이용약관'

                }


            }
        },
        updated() {
            /*
            JQ('.content h2 > span').css('font-size', '1.15em');
            JQ('.content .ql-size-small').css('font-size', '0.75em');
            JQ('.content .ql-size-small').css('line-height', '130%');
            JQ('.content .ql-size-large').css('font-size', '1.5em');
            JQ('.content .ql-size-large').css('line-height', '130%');
            JQ('.content .ql-size-huge').css('font-size', '2.5em');
            JQ('.content .ql-size-huge').css('line-height', '130%');
            JQ('.content p').css({'line-height': '130%', 'margin': 0});
    */

        }

    }
</script>


<style lang="scss" scoped>
    .login-container {
        width: 970px;
        margin: 50px auto;

        .login-title {
            text-align: center;
            color: #000;
            font-size: 30px;
            font-weight: bold;
            margin-bottom: 45px;
        }

        .content {
            background: #fff;
            min-height: calc(100vh - 200px);
            max-height: calc(100vh + 200px);
            border: solid 1px #dcdcdc;
            overflow: auto;
            padding: 50px;
        }

        &.mobile {

            width: 100%;
            margin: 50px 0;
            padding: 0 20px;

            .login-title {
                display: none;
            }

            .content {
                padding: 20px;
            }
        }
    }
</style>